import React from 'react';
import { Card, CardBody, CardTitle, CardText, CardImg, Breadcrumb, BreadcrumbItem, Button, Modal, ModalBody, ModalHeader, Label, Row } from 'reactstrap';
import { LocalForm, Control, Errors } from 'react-redux-form';
import { Link } from 'react-router-dom';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';
import { motion } from 'framer-motion';

class CommentForm extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            isModalOpen: false
        }
    }
    toggleModal = () => {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    handleSubmit = (values) => {
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
        this.toggleModal();
    }
    handleFocus = (element) => {
        if (element.value === '') element.classList.add('is-invalid')
    }
    handleChange = (func, element) => {
        const val = element.value;
        if (func(val)) element.classList.add('is-invalid');
        else {
            element.classList.remove('is-invalid')
            element.classList.add('is-valid')
        };
    }
    render() {
        // For all inputs
        const required = (val) => val && val.length;
        // For the name and the comment
        const minLength = (val, len) => val && (val.length >= len);
        // For the name
        const minLengthName = (val) => (!required(val) || !validName(val)) || minLength(val, 3);

        // For the name
        const maxLength = (val, len) => !(val) || (val.length < len)
        const maxLengthName = (val) => (!required(val) || !validName(val)) || maxLength(val, 15);
        const validName = (val) => !required(val) || (/^[A-Z ]+$/i.test(val));
        // Specialy for comment
        const minLengthComment = (val) => (!required(val) || !validComment(val)) || minLength(val, 15);
        const validComment = (val) => !required(val) || (/^[A-Z!0-9 \n]+$/i.test(val));
        // Check if the input is invalid to add bootstrap classes to it
        const invalidNameInput = (val) => !required(val) || !minLengthName(val) || !maxLengthName(val) || !validName(val);
        const invalidCommentInput = (val) => !required(val) || !minLengthComment(val) || !validComment(val);
        
        return (
            <>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <div className="container">
                            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                                <Row className="form-group">
                                    <Label htmlFor="rating" name="rating"> Rating</Label>
                                    <Control.select className="form-control" name="rating" model=".rating">
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Control.select>
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="author" name="author">Your Name</Label>
                                    <Control.text className="form-control" id="author" name="author" model=".author" 
                                    placeholder="Your Name"
                                    onChange={() => {
                                        const element = document.querySelector('#author');
                                        this.handleChange(invalidNameInput, element);
                                        
                                    }}
                                    onFocus={() => {
                                        const element = document.querySelector('#author');
                                        this.handleFocus(element);
                                    }}
                                    validators={{
                                        required, minLengthName, maxLengthName,
                                        validName
                                    }} />
                                    <Errors className="text-danger" show="touched" model=".author"
                                    messages={{
                                        required: 'Required*',
                                        minLengthName: 'Name should be greater than 3 characters',
                                        maxLengthName: 'Name should be 15 characters or less',
                                        validName: 'Name is invalid'
                                    }} />
                                </Row>
                                <Row className="form-group">
                                    <Label htmlFor="comment" name="comment">Comment</Label>
                                    <Control.textarea className="form-control" name="comment" id="comment" model=".comment" rows="6"
                                    onChange={() => {
                                        const element = document.querySelector('#comment');
                                        this.handleChange(invalidCommentInput, element);
                                        
                                    }}
                                    onFocus={() => {
                                        const element = document.querySelector('#comment');
                                        this.handleFocus(element);
                                    }}
                                    validators={{
                                        required, minLengthComment,
                                        validComment
                                    }} />
                                    <Errors className="text-danger" show="touched" model=".comment"
                                    messages={{
                                        required: 'Required*',
                                        minLengthComment: 'Your comment should be at least 15 characters',
                                        validComment: 'Your comment should not contain special characters like: ;, #, @, / and \\'
                                    }} />
                                </Row>
                                <Row className="form-group">
                                    <Button type="submit" color="primary">Submit</Button>
                                </Row>
                            </LocalForm>
                        </div>
                    </ModalBody>
                </Modal>
                <Button outline onClick={this.toggleModal}><span className="fas fa-pen"></span> Submit a Comment</Button>
            </>
        );
    }
}

function RenderDish({dish}) {                                
    return (
        <div className="col-12 col-md-5 m-1">
            <FadeTransform in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                    }}>
                <Card>
                    <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        </div>
    );       
}  

function RenderComments({comments, postComment, dishId }) {        
    const COMMENT = comments.map(comment => {
        return (
            <Fade in>
                <div key={comment.id}>
                    <ul className="list-unstyled">
                        <li>{comment.comment}</li>
                        <br />
                        <li>--{comment.author},{/* Converting to the required format */} {new Intl.DateTimeFormat('en-US', {
                        year: 'numeric',
                        month: 'short',
                        day: '2-digit'
                    }).format(new Date(Date.parse(comment.date)))}</li>
                    </ul>
                </div>
            </Fade>
        );
    });
    return (
        <div className="col-12 col-md-5 m-1">
            <h4>Comments</h4>
            <Stagger in>
                {COMMENT}
            </Stagger>
            <CommentForm postComment={postComment} dishId={dishId} />            
        </div>
    );                             
}  

const DishDetail = (props) => {  
    if (props.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    } else if (props.errMess) {
        return (
            <h4>{props.errMess}</h4>
        );
    } else if (props.dish != null) {
        return (
            <motion.div initial={{x: '-100%'}} animate={{x: '0'}} exit={{x: '-100%'}} transition={{ease: "easeInOut"}} className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h4>{props.dish.name}</h4>
                        <hr />
                    </div>
                </div>
                <div className="row">                    
                    <RenderDish dish={props.dish} />
                    <RenderComments comments={props.comments}
                    dishId={props.dish.id}
                    postComment={props.postComment} />
                </div>
            </motion.div>
        );
    } else {
        return (
            <div></div>
        );
    }
        
}

export default DishDetail;