import React, { Component } from 'react';
import { Breadcrumb, BreadcrumbItem, Button, Row, Col, Label } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Form, Control, Errors} from 'react-redux-form';
import { motion } from 'framer-motion';
import { Loading } from './LoadingComponent';

// For all inputs
const required = (val) => val && val.length;
// For the name and message
const minLength = (val, len) => val && (val.length >= len);
// For the name
const minLengthName = (val) => (!required(val) || !validName(val)) || minLength(val, 3);
// For the name
const maxLength = (val, len) => !(val) || (val.length < len)
const maxLengthName = (val) => (!required(val) || !validName(val)) || maxLength(val, 8);
// For the message
const minLengthMessage = (val) => !required(val) || minLength(val, 100);
// Name Validation
const validName = (val) => !required(val) || /^[A-Z]+$/i.test(val);
// Email Validation
const validEmail = (val) => !required(val) || (/^([A-Z0-9._-]+)@([A-Z0-9_.-]+)\.([A-Z]{2,4})$/i.test(val) && !/\.\./.test(val) && !/@\./.test(val) && !/\.@/.test(val) && !/@-/.test(val) && !/@_/.test(val) && !/-@/.test(val) && !/_@/.test(val) && !/\.-/.test(val) && !/\._/.test(val) && !/-\./.test(val) && !/_\./.test(val));
// For tel.number
const exactLength = (val, len) => val && (val.length === len);
const exactLengthTel = (val) => (!required(val) || !validTel(val)) || exactLength(val, 10);
// Tel. Number validation
const validTel = (val) => !required(val) || /^\d+$/i.test(val);

// Check if the input is invalid to add bootstrap classes to it
const invalidNameInput = (val) => !required(val) || !minLengthName(val) || !maxLengthName(val) || !validName(val);
const invalidTelInput = (val) => !required(val) || !validTel(val) || !exactLengthTel(val)
const invalidEmailInput = (val) => !required(val) || !validEmail(val);
const invalidMessageInput = (val) => !required(val) || !minLengthMessage(val);

class Contact extends Component {
    
    handleSubmit = (values) => {
        this.props.resetFeedbackForm();
        this.props.postFeedback(values.firstname, values.lastname, values.telnum, values.email, values.agree, values.contactType, values.message);
        const inputs = document.querySelectorAll('input, textarea');
        for (let i of inputs) {
            // i.value = '';
            i.classList.remove('is-valid');
            i.classList.remove('is-invalid');
        }
    }
    handleBlur = (func, element) => {
        const val = element.value;
        if (func(val)) element.classList.add('is-invalid');
        else {
            element.classList.remove('is-invalid')
            element.classList.add('is-valid')
        };
    }
    render() {
        if (this.props.isLoading) {
            return (
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        } else {
            return(
                <motion.div initial={{x: '-100%'}} animate={{x: '0'}} exit={{x: '-100%'}} transition={{ease: "easeInOut"}} className="container">
                        <div className="row">
                            <Breadcrumb>
                                <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                                <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                            </Breadcrumb>
                            <div className="col-12">
                                <h4>Contact Us</h4>
                                <hr />
                            </div>
                        </div>
                        <div className="row row-content">
                            <div className="col-12">
                            <h3>Location Information</h3>
                            </div>
                            <div className="col-12 col-sm-4 offset-sm-1">
                                    <h5>Our Address</h5>
                                    <address>
                                    121, Clear Water Bay Road<br />
                                    Clear Water Bay, Kowloon<br />
                                    HONG KONG<br />
                                    <i className="fa fa-phone"></i>: +852 1234 5678<br />
                                    <i className="fa fa-fax"></i>: +852 8765 4321<br />
                                    <i className="fa fa-envelope"></i>: <a href="mailto:confusion@food.net">confusion@food.net</a>
                                    </address>
                            </div>
                            <div className="col-12 col-sm-6 offset-sm-1">
                                <h5>Map of our Location</h5>
                            </div>
                            <div className="col-12 col-sm-11 offset-sm-1">
                                <div className="btn-group" role="group">
                                    <a role="button" className="btn btn-primary" href="tel:+85212345678"><i className="fa fa-phone"></i> Call</a>
                                    {/*eslint-disable-next-line*/}
                                    <a role="button" className="btn btn-info"><i className="fab fa-skype"></i> Skype</a>
                                    <a role="button" className="btn btn-success" href="mailto:confusion@food.net"><i className="far fa-envelope"></i> Email</a>
                                </div>
                            </div>
                        </div>
                        <div className="row row-content">
                            <div className="col-12">
                                <h3>Send us Your Feedback</h3>
                            </div>
                            <div className="col-12 col-md-9">
                                <Form model="feedback" onSubmit={(values) => this.handleSubmit(values)}>
                                    <Row className="form-group">
                                        <Label htmlFor="firstname" md={2}>First Name</Label>
                                        <Col md={10}>
                                            <Control.text model=".firstname" className="form-control" id="firstname" name="firstname" placeholder="First Name"
                                            validators={{
                                                required, minLengthName, maxLengthName, validName
                                            }} 
                                            onBlur={() => {
                                                const element = document.querySelector('#firstname');
                                                this.handleBlur(invalidNameInput, element);
                                            }} />
                                            <Errors className="text-danger"
                                                model=".firstname"
                                                show='touched'
                                                messages={{
                                                    required: 'Required*',
                                                    minLengthName: 'First name should be at least 3 characters',
                                                    maxLengthName: 'First name should be 8 characters or less',
                                                    validName: 'Name is invalid'
                                                }}
                                                    />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="lastname" md={2}>Last Name</Label>
                                        <Col md={10}>
                                            <Control.text model=".lastname" className="form-control" id="lastname" name="lastname" placeholder="Last Name"
                                            validators={{
                                                required, minLengthName, maxLengthName, validName
                                            }}
                                            onBlur={() => {
                                                const element = document.querySelector('#lastname');
                                                this.handleBlur(invalidNameInput, element);
                                            }} />
                                            <Errors className="text-danger"
                                                model=".lastname"
                                                show='touched'
                                                messages={{
                                                    required: 'Required*',
                                                    minLengthName: 'Last name should be at least 3 characters',
                                                    maxLengthName: 'Last name shold be 8 characters or less',
                                                    validName: 'Name is invalid'
                                                }} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="telnum" md={2}>Contact Tel.</Label>
                                        <Col md={10}>
                                            <Control.text model=".telnum" id="telnum" name="telnum" placeholder="Tel."
                                            className="form-control"
                                            onBlur={() => {
                                                const element = document.querySelector('#telnum');
                                                this.handleBlur(invalidTelInput, element);
                                            }}
                                            validators={{
                                                required, exactLengthTel,
                                                validTel
                                            }}  />
                                            <Errors className="text-danger"
                                                model=".telnum"
                                                show='touched'
                                                messages={{
                                                    required: 'Required* ',
                                                    exactLengthTel: 'Tel. number should be 10 numbers\n\n',
                                                    validTel: 'Tel. number should be numbers only'
                                                }} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="email" md={2}>Email Address</Label>
                                        <Col md={10}>
                                            <Control.text model=".email" id="email" name="email" placeholder="Email Address"
                                            className="form-control"
                                            onBlur={() => {
                                                const element = document.querySelector('#email');
                                                this.handleBlur(invalidEmailInput, element);
                                            }}
                                            validators={{
                                                required, validEmail
                                            }}  />
                                            <Errors className="text-danger"
                                                model=".email"
                                                show='touched'
                                                messages={{
                                                    required: 'Required*',                    
                                                    validEmail: 'Email Address is invalid'
                                                }} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col md={{size: 6, offset: 2}}>
                                            <div className="form-check">
                                                <Label check>
                                                    <Control.checkbox model=".agree" name="agree" className="form-check-input" /> {' '}
                                                    <strong>May we contact you?</strong>
                                                </Label>
                                            </div>
                                        </Col>
                                        <Col md={{size: 3, offset: 1}}>
                                            <Control.select model=".contactType" name="contactType"
                                            className="form-control">
                                                <option>Tel.</option>
                                                <option>Email</option>
                                            </Control.select>
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="message" md={2}>Your Feedback</Label>
                                        <Col md={10}>
                                            <Control.textarea model=".message" id="message" name="message" rows="12"
                                            className="form-control"
                                            onBlur={() => {
                                                const element = document.querySelector('#message');
                                                this.handleBlur(invalidMessageInput, element);
                                            }} 
                                            validators={{
                                                required, minLengthMessage
                                            }} />
                                            <Errors className="text-danger"
                                                model=".message"
                                                show='touched'
                                                messages={{
                                                    required: 'Required*',                    
                                                    minLengthMessage: 'Your feedback should be at least 100 characters'
                                                }} />
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Col md={{size: 10, offset: 2}}>
                                            <Button type="submit" color="primary">
                                                Send Feedback
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form>
                            </div>
                        </div>
                    </motion.div>
            );
        }                                     
    }
}

export default Contact;