import React, { Component } from 'react';
import { Nav, Navbar, NavbarBrand, NavItem, NavbarToggler, Collapse, Jumbotron, Modal, ModalHeader, ModalBody, Form, FormGroup, Button, Label, Input, FormFeedback} from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {
    constructor(props) {
        super(props)
        
        this.state = {
            isNavOpen: false,
            isModalOpen: false,
            touched: {
                username: false,
                password: false
            }
        }        
    }

    toggleNav = () => {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }
    toggleModal = () => {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleLogin = (event) => {
        this.toggleModal();
        alert(`Username: ${ this.username.value } Password: ${ this.password.value } Remember: ${ this.remember.checked }`)
        event.preventDefault();
    }

    handleBlur = (field) => (evt) => {
        this.setState({
            touched: {...this.state.touched, [field]: true}
        })
    }

    // Form validation
    validate = (username, password) => {
        
        if(username !== null && password !== null) {
            const errors = {
                username: '',
                password: ''
            }
            // Username validation
            // Defining the Regular expression to do the job
            const userReg = /^[a-zA-Z0-9_]+$/;
            // If username is empty
            if(this.state.touched.username && username.value === '') errors.username = 'Username should not be empty';
            // If username is short
            else if(this.state.touched.username && username.value.length <= 8) errors.username = 'Username should be at least 8 characters';
            // If username is invailed
            else if(this.state.touched.username && !userReg.test(username.value)) errors.username = 'Username should contain characters, numbers and _ only';
                        
            // Password Validation
            // If password is empty
            if(this.state.touched.password && password.value === '') errors.password = 'Password should not be empty';
            // If password is short
            else if(this.state.touched.password && password.value.length <= 8) errors.password = 'Password should be at least 8 characters';
            // If password did not contain capital characters
            else if(this.state.touched.password && !/[A-Z]/.test(password.value)) errors.password = 'Password should contain at least one capital character';
            // If password did not contain small characters
            else if(this.state.touched.password && !/[a-z]/.test(password.value)) errors.password = 'Password should contain at least one small character';
            // If password did not contain numbers
            else if(this.state.touched.password && !/[0-9]/.test(password.value)) errors.password = 'Password should contain numbers';
            // Retrun the errors
            return errors;
        }
    }

    render() {
        const errors = this.validate(this.username, this.password);
        return(
        <>
            <Navbar dark expand="md">
                <div className="container">
                    <NavbarToggler onClick={this.toggleNav} />
                    <NavbarBrand className="mr-auto" href="/">
                        <img src="assets/images/logo.png" height="30" width="41" alt="Ristorante Con Fusion" />
                    </NavbarBrand>
                    <Collapse isOpen={this.state.isNavOpen} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-link" to="/home">
                                    <span className="fa fa-home fa-lg"></span> Home
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/aboutus">
                                    <span className="fa fa-info fa-lg"></span> About Us
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/menu">
                                    <span className="fa fa-list fa-lg"></span> Menu
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to="/contactus">
                                    <span className="fa fa-address-card fa-lg"></span> Contact Us
                                </NavLink>
                            </NavItem>
                        </Nav>
                        <Nav className="ml-auto" navbar>
                            <NavItem>
                                <Button outline onClick={this.toggleModal}>
                                    <span className="fas fa-sign-in-alt"></span> Login
                                </Button>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </div>
            </Navbar>
            <Jumbotron>
                <div className="container">
                    <div className="row row-header">
                        <div className="col-12 col-sm-6">
                            <h1>Ristorante Con Fusion</h1>
                            <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                        </div>
                    </div>
                </div>
            </Jumbotron>
            <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                        <FormGroup>
                            <Label htmlFor="username">Username</Label>
                            <Input type="text" name="username" id="username"
                            onBlur={this.handleBlur('username')}
                            valid={errors !== undefined && this.state.touched.username ? errors.username === '' : null}
                            invalid={errors !== undefined ? errors.username !== '' : null} 
                            innerRef={input => this.username = input} />
                            <FormFeedback>{errors !== undefined ? errors.username : null}</FormFeedback>
                        </FormGroup>
                        <FormGroup>
                            <Label htmlFor="password">Password</Label>
                            <Input type="password" name="password" id="password"
                            onBlur={this.handleBlur('password')}
                            valid={errors !== undefined && this.state.touched.password ? errors.password === '' : null}
                            invalid={errors !== undefined ? errors.password !== '' : null}  
                            innerRef={input => this.password = input} />
                            <FormFeedback>{errors !== undefined ? errors.password : null}</FormFeedback>
                        </FormGroup>
                        <FormGroup check>
                            <Label check>
                                <Input type="checkbox" name="remember" 
                                innerRef={input => this.remember = input} /> Remember me
                            </Label>
                        </FormGroup>
                        <Button type="submit" value="submit" color="primary">Login</Button>
                    </Form>
                </ModalBody>
            </Modal>
        </>
        );
    }
}
export default Header;